import Title from '../../components/Title'
import Header from '../../components/Header'
import { FiEdit } from 'react-icons/fi'
import { toast } from 'react-toastify'
import {useHistory, useParams} from 'react-router-dom'

import {useState, useEffect} from 'react'  
import firebase from '../../services/firebaseConnection'



export default function Editar() {
    const { id } = useParams()
    const history = useHistory()
// Estados
    const [clientes, setClientes] = useState( [] )
    const [loading_Cli, setLoading_Cli] = useState( true )
    const [clienteSelect, setClienteSelect] = useState( 0 )
    const [servico, setServico] = useState( 'Avaliacao' )
    const [status, setStatus] = useState( 'Aberto' )
    const [complemento, setComplemento] = useState( '' )
    const [idCliente, setIdCliente] = useState(false) // false pq não foi editado

    useEffect( () => {
        // Vai trazer cliente la do firebase quando aplicação abrir 
            async function carregaClientes_Firebase() {
              await firebase.firestore().collection( 'clientes' )
                .get() //pegar
                .then( (snapshot) => { // caso de sucesso, recebe snpashot de cliente
                  let lista = []
                  snapshot.forEach( ( doc ) => { //dentro de cliente, para cada documento da um push em obj
                    lista.push( {
                      id: doc.id,
                      nome: doc.data().nome
        
                    })
                  } )
        
                  // Verificando se lista não esta vazia
                  if ( lista.length === 0 ) {
                    console.log( 'Nenhum cliente encontrado' );
                    setClientes( [{ id: '1', cliente: ''}])
                    setLoading_Cli( false )
                    return;
                  }
                  setClientes( lista )
                  setLoading_Cli(false)
                  
                // Veficando se quer editar chamado
                    if ( id ) {
                    // chama função
                    loadId(lista)
                }    
                    
                } )
                .catch( ( erro ) => {
                  console.log( erro );
                  setLoading_Cli( false )
                  setClientes( [{ id: '1', cliente: ''}])
                })
          }
          carregaClientes_Firebase()
        },[id])

    // chamado quando for editar chamado
    async function loadId(lista) {
        // Vendo se id passado está no firabase
        await firebase.firestore().collection( 'chamados' ).doc( id ) // acessando doc com id passado
            .get()
            // vai acessar o snapshot e enviar para os estados a propriedade contendo o valor
            .then( ( snapshot ) => {
                setServico( snapshot.data().servico )
                setStatus( snapshot.data().status )
                setComplemento( snapshot.data().complemento )
                
                // Achando o cliente usando o id passado
                let index = lista.findIndex(item => item.id === snapshot.data().clienteID)
                setClienteSelect( index )
                // Se conseguiu achar, então está editando
                setIdCliente(true)
            } )
            .catch( ( erro ) => {
                console.log( erro );
                toast.error('ID não encontrado')
                setIdCliente(false)
            })
    }
    // Chamado depois que editou e clicou em salvar
    async function handleAtualizarChamado(e) {
        e.preventDefault()
    
        //Se id passado ja foi validado
        if ( idCliente ) {
            await firebase.firestore().collection( 'chamados' )
                .doc( id )
                .update( {
                    servico: servico,
                    status: status,
                    complemento: complemento
                } )
                .then( () => {
                    toast.success( 'Chamado editado com sucesso!' )
                    setStatus( 'Aberto' )
                    setServico('Avaliacao')
                    setComplemento( '' )
                    // navegando usuario de volta dashboard
                    history.push('/dashboard')
                } )
                .catch( ( erro ) => {
                    console.log( erro );
                    toast.error('Ops, erro ao tentar salvar, tente mais tarde')
                } )
                
            return;
        }
    }
    
    // chamado quando troca cliente
  function handleTrocaCliente( e ) {
    console.log( 'index do cli selecionado', e.target.value );
    console.log( 'Cliente selecionado', clientes[e.target.value] );
    setClienteSelect(e.target.value)
  }
    // Chamado quando troca serviço e manda pra state
  function trocaServico_Select( e ) {
    setServico( e.target.value )
    console.log(e.target.value);
  }
    // Chamado quando troca o status, e manda para o state
  function trocaStatusRadio( e ) {
    setStatus(e.target.value)
    console.log(e.target.value);
  }

 return (
   <div>
         <Header />
         <div className='content'>
            <Title name='Editar Chamado'>
            <FiEdit size={25}/>
             </Title>

             <div className='container'>
                 <form className='form-profile' onSubmit={handleAtualizarChamado}>
                     <label className='tipo'>Pessoa Física / Empresa - cnpj</label>
                     {loading_Cli ? (
                <input type='text' disabled={true} value='Carregando clientes...' />
              ) : (
                      <select value={clienteSelect} onChange={handleTrocaCliente}>
                      {clientes.map( ( item, index ) => {
                        return (
                          <option key={item.id} value={index}>
                            {item.nome}
                          </option>
                        )
                        })}
                      </select>
                )}
                     
                     <label>Serviço</label>
              <select value={servico} onChange={trocaServico_Select}>
                <option value='Avaliacao'>Avaliação</option>
                <option value='Revisao'>Revisão</option>
                <option value='Revisao obrigatoria'>Revisão obrigatória</option>
                <option value='Troca de Bateria'>Troca de bateria</option>
                <option value='Alinhamento/Balanceamento'>Alinhamento/Balanceamento</option>
                <option value='Filtro/óleo'>Filtro/óleo</option>
                <option value='Freios'>Freios</option>
                <option value='Supensao'>Supensão</option>
                <option value='Escapamento'>Escapamento</option>
                <option value='Motor'>Motor</option>
                <option value='Cambio'>Câmbio</option>
                <option value='Eletrica'>Eletrica</option>
                <option value='Computador de bordo'>Computador de bordo</option>
                <option value='Remap'>Remap</option>
                <option value='Troca de peca'>Troca de peças</option>
              </select>

              <label>Status</label>
              <div className='status'>
                <input
                  type='radio'
                  name='radio'
                  value='Aberto'
                  onChange={trocaStatusRadio}
                  checked={status === 'Aberto'}
                />
                <span>Em Aberto</span>

                <input
                  type='radio'
                  name='radio'
                  value='Progresso'
                  onChange={trocaStatusRadio}
                  checked={status === 'Progresso'}
                />
                <span>Em Progresso</span>
                <input
                  type='radio'
                  name='radio'
                  value='Finalizado'
                  onChange={trocaStatusRadio}
                  checked={status === 'Finalizado'}
                />
                <span>Finalizado</span>
                     </div>
                     <label>Complemento</label>
              <textarea
                type='text'
                placeholder='Observações (Opcional).'
                value={complemento}
                onChange={(e)=> setComplemento(e.target.value)}
              />
              
              <button className='cadastrar' type='submit'>Salvar</button>
                </form>
             </div>
        </div>
   </div>
 );
}